.recepie{ 
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    margin-top:1.5rem;
    padding-bottom: 3rem;
    border-radius: 10px;
    /* background-color:green; */
    background:linear-gradient(to bottom right,rgba(255,255,255,0.2),rgba(255,255,255,0.05));
    -webkit-backdrop-filter:blur(5px);
            backdrop-filter:blur(5px);
    box-shadow:0 1rem 2rem rgba(0,0,0,0.5),-1px -1px 2px #aaa,1px 1px 2px #555;
    /* box-shadow: -1rem 0 3rem #000; */
    transition: .1s;    
}

.card-header{
    font-size: larger;
    font-weight: 600;
    margin-top: .4rem;
    width: 200px;
    margin-bottom: .4rem;
    text-transform: capitalize;
    transition: .3s;
}

.card-header:hover{
    color: black;
    transform: scale(1.1);
    text-shadow: 2px 2px 5px orangered;
    /* background: linear-gradient(90deg,#ff8a00,#e52ee5);
    text-shadow: none;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color:transparent; */
}

.imgClass{
    /* max-width: 200px;
    min-width: 100px;
    max-height: 200px;
    min-height: 100px; */
    width: 200px;
    height: 200px;
    
    display: block;
    margin-bottom: .1rem;
    /* width: 150px;
    height: 150px; */
    transition: .2s;
    border-radius: 25%;
}
.imgClass:hover{
   transform: scale(1.05);
   box-shadow: 2px 2px 4px black;
 
}
.ingrediants{
    margin: .1rem;
    display: inline-block;
    
    width: 200px;
    text-align:start;
}
.ingrediants:hover{
    color: black;
    transform: scale(1.1);
    text-shadow: 2px 2px 5px orangered;
   
}

.recepie:hover{
    transform: translateY(-1rem);
}
.recepie:hover~.recepie{
    transform: translateX(130px);
}

.recepie:not(:first-child){
    margin-left:-130px;
}
.recepie:hover .card-header{
    color: black;
    font-size:larger;
    text-shadow: 2px 2px 5px orangered;
}

.recepie:hover .ingrediants{
    color: black;
    font-size:larger;
    text-shadow: 2px 2px 5px orangered;
}
@media only screen and (max-width: 720px) {
 
    .recepie{ 
        display: flex;
        flex-direction: column;
        position: relative;
        align-items: center;
        justify-content: center;
        height: auto;
        width: auto;
        border-radius: 10px;
        background:linear-gradient(to bottom right,rgba(255,255,255,0.2),rgba(255,255,255,0.05));
         -webkit-backdrop-filter:blur(5px);
                 backdrop-filter:blur(5px);
        box-shadow:0 1rem 2rem rgba(0,0,0,0.5),-1px -1px 2px #aaa,1px 1px 2px #555;
       
    
        /* box-shadow: 0px 0px 10px whitesmoke; */
        transition: .1s;
       
        /* border-spacing: 10px; */
        
    }
    .recepie:not(:first-child){
        margin-left:0px;
    }
    
    .recepie:hover{
        transform: translateX(0rem);
    }
    .recepie:hover~.recepie{
        transform: translateX(0px);
    }
   
   
    /* .recepie:hover~.recepie{
        
    } */
