.container {
  color:#999;
  text-transform: uppercase;
  font-size:36px;
  font-weight:bold;
  margin: 1rem;
  padding: 1rem;
  display:flex;
  flex-direction: row;
  align-items: center;
  justify-content:center;
  position: relative;

}

.flip {
  height:5rem;
  overflow:hidden;
  margin: .5rem;
  padding: .5rem;
}

.flip > div > div {
  color:#fff;
  padding:4px 12px;
  height:45px;
  margin-bottom:45px;
  display:inline-block;
}

.flip div:first-child {
  -webkit-animation: show 4s linear infinite;
          animation: show 4s linear infinite;
}

.flip div div {
  background:#42c58a;
  border-radius: 5px;
}
.flip div:first-child div {
  background:#4ec7f3;
  border-radius: 5px;
}
.flip div:last-child div {
  background:#DC143C;
  border-radius: 5px;
}

@-webkit-keyframes show {
  0% {margin-top:-270px;}
  5% {margin-top:-180px;}
  33% {margin-top:-180px;}
  38% {margin-top:-90px;}
  66% {margin-top:-90px;}
  71% {margin-top:0px;}
  99.99% {margin-top:0px;}
  100% {margin-top:-270px;}
}

@keyframes show {
  0% {margin-top:-270px;}
  5% {margin-top:-180px;}
  33% {margin-top:-180px;}
  38% {margin-top:-90px;}
  66% {margin-top:-90px;}
  71% {margin-top:0px;}
  99.99% {margin-top:0px;}
  100% {margin-top:-270px;}
}

